import React from 'react'
import { ArkConfig } from 'ark-react-config'

const App = () => {
  return <div>
    <br />
    Testing Ark Config to read the global variable
    <br />
    <br />
    <pre>
      {
        `
      var obj = {
        one: "Lord",
        two: {
          two: "Jesus"
        },
        three1: {
          three2: {
            three3: "my Saviour"
          }
        }
      }
`
      }
    </pre>
    <br />
    Get Nested Property Primitive Value : <input type="text" placeholder="enter key e: (prop1.prop2.prop3)" defaultValue={ArkConfig.get('obj.one') + ' ' + ArkConfig.get('obj.two.two') + ' ' + ArkConfig.get('obj.three1.three2.three3')} />
    <pre>
      <code>
        {"{ArkConfig.get('obj.one') + ' ' + ArkConfig.get('obj.two.two') + ' ' + ArkConfig.get('obj.three1.three2.three3')}"}
      </code>
    </pre>
    <br />
    Get Nested Property Object Value : <input type="text" placeholder="enter key e: (prop1.prop2.prop3)" defaultValue={JSON.stringify(ArkConfig.get('obj.two'))} />
    <pre>
      <code>
        {"{JSON.stringify(ArkConfig.get('obj.two'))}"}
      </code>
    </pre>
    <br />
    <br />
    Non Exsiting Prop: <input type="text" placeholder="enter key e: (prop1.prop2.prop3)" defaultValue={ArkConfig.get('obj.nonexist.multi.layer.prop')} />
    <br />
    Set Nested Property Primitive Value : <input type="text" placeholder="enter key e: (prop1.prop2.prop3)" defaultValue={ArkConfig.set('obj.one', 'Lord Jesus my Protector')} />
    <button onClick={() => ArkConfig.set('obj.one', 'Lord Jesus my Master')}>Set Val</button>
    <button onClick={() => alert(ArkConfig.read('obj.one'))}>Get Val</button>
    <pre>
      <code>
        {"{JSON.stringify(ArkConfig.get('obj.one'))}"}
      </code>
    </pre>
    <br />
  </div>
}

export default App
