import React from 'react'
var arkw = window;
export const ArkConfig = (() => {
  const get = key => (key || '').split('.').reduce((p, c) => p && p[c] || null, arkw);
  const set = (key, val) => {
    var lst = (key || '').split('.');
    if (!lst.length) return;
    var cur = arkw;
    lst.forEach(t => {
      cur[t] = cur[t] || {};
      cur = cur[t];
    });
    cur = val;
  }
  return {
    get: get,
    set: set
  }
})();
